@use 'variables' as v;
html {
    box-sizing: border-box;
    font-size: 62.5%;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$font;
    font-size: 1.6rem;
}
p {
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.5;
}
.contenedor {
    width: min(90%, 120rem);
    margin: 0 auto;
}
a {
    text-decoration: none;
    color: v.$negro;
}
h1, h2, h3 {
    margin: 0 0 5rem 0;
    font-weight: normal;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
img,
picture {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}