html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: min(90%, 120rem);
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
  font-weight: normal;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img,
picture {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 1rem;
}
.barra-logo {
  flex: 0;
  margin: 0;
  font-weight: normal;
  text-transform: uppercase;
  font-family: "Black Ops One", cursive;
  border-left: 5px solid #d34e24;
  font-size: 5rem;
}
.barra-buscador {
  order: 3;
  flex-shrink: 2;
  width: 100%;
}
@media (min-width: 768px) {
  .barra-buscador {
    width: auto;
    order: 0;
  }
}
.barra-navegacion {
  flex: 0;
}
.barra-navegacion picture {
  width: 6rem;
  border-radius: 50px;
  overflow: hidden;
}

.formulario {
  -webkit-box-shadow: -4px 2px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -4px 2px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: -4px 2px 5px 0px rgba(0, 0, 0, 0.75);
  text-align: center;
  margin-top: 2rem;
  background-color: #e1e1e1;
  padding: 1rem;
}
@media (min-width: 768px) {
  .formulario {
    background-color: transparent;
    box-shadow: none;
  }
}
.formulario label {
  display: block;
  margin-bottom: 0.5rem;
}
@media (min-width: 768px) {
  .formulario label {
    display: inline;
  }
}
.formulario input {
  padding: 1rem;
  border: 0.1rem solid #d34e24;
  border-radius: 1.5rem;
}
.formulario button {
  padding: 1rem;
  border-radius: 1.5rem;
  border: none;
  background-color: #d34e24;
  color: #ffffff;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}
.formulario button:hover {
  box-shadow: 1px 1px 2px 1px #000000;
}

.tablero {
  border-radius: 1.5rem;
  background-color: #e1e1e1;
  min-height: 20rem;
  max-height: 50rem;
  padding: 2rem;
  margin-top: 2rem;
  overflow-y: scroll;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  justify-items: center;
}
@media (min-width: 768px) {
  .tablero {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}
@media (min-width: 992px) {
  .tablero {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
  }
}
@media (min-width: 1200px) {
  .tablero {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 1rem;
  }
}

.vehiculo {
  border: 1px solid #563f1b;
  border-radius: 1.5rem;
  width: 100%;
  height: 7rem;
  padding: 1.5rem;
}
.vehiculo p {
  margin: 0;
  text-align: center;
}
.vehiculo-matricula {
  font-weight: bold;
}
.vehiculo-ubicacion {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.vehiculo-ubicacion p {
  font-size: 1.4rem;
}